import { Currency } from "@/types/currency";
import { create } from "zustand";
import { persist } from "zustand/middleware";
import { UTILS_STORAGE_KEY } from "../constants";

interface UtilsState {
  preferredCurrency: Currency | null;
  setPreferredCurrency: (preferredCurrency: Currency) => void;
}

export const useUtilsStore = create<UtilsState>()(
  persist(
    (set) => ({
      preferredCurrency: null,
      setPreferredCurrency: (preferredCurrency: Currency) =>
        set({ preferredCurrency }),
    }),
    {
      name: UTILS_STORAGE_KEY, // name of the item in localStorage
    }
  )
);
