"use client";

import useSkillQuery from "@/hooks/useSkillQuery";
import { getCurrencies } from "@/lib/api/currencyApi";
import { useUtilsStore } from "@/lib/store/utilsStore";
import { Currency } from "@/types/currency";
import { ChevronDown } from "lucide-react";
import { Button } from "../ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "../ui/dropdown-menu";

const CurrencySelector = () => {
  // #region API calls
  const { data: currencies } = useSkillQuery({
    queryKey: ["currencies"],
    queryFn: () => getCurrencies(),
  });
  // #endregion

  const { preferredCurrency, setPreferredCurrency } = useUtilsStore();

  const currencySelector = (currency: Currency) => {
    setPreferredCurrency(currency);
  };

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button
          variant="ghost"
          size="sm"
          className="flex items-center space-x-2"
        >
          {preferredCurrency?.symbol}
          <span className="text-sm font-medium">
            {preferredCurrency?.code || "Select Preferred Currency"}
          </span>
          <ChevronDown className="h-3 w-3" />
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end" className="w-48">
        <DropdownMenuLabel>Select Currency</DropdownMenuLabel>
        <DropdownMenuSeparator />
        {currencies?.data.map((currency) => (
          <DropdownMenuItem
            key={currency.code}
            className="flex items-center justify-between"
            onClick={() => currencySelector(currency)}
          >
            <div className="flex items-center space-x-2">
              <span className="font-medium text-lg">{currency.symbol}</span>
              <div className="flex flex-col">
                <span className="text-sm font-medium">{currency.code}</span>
                <span className="text-xs text-muted-foreground">
                  {currency.name}
                </span>
              </div>
            </div>
          </DropdownMenuItem>
        ))}
      </DropdownMenuContent>
    </DropdownMenu>
  );
};

export default CurrencySelector;
